<template>
  <section>
    <h1
      class="title is-2"
      v-if="isRegistered"
    >You're Registered , Have a nice time for waiting while we are processing your data ðŸŽ‰ ðŸŽ‰ ðŸŽ‰</h1>

    <form action @submit.prevent="register" v-if="!isRegistered">
      <b-field label="Name">
        <b-input v-model="user.name" readonly></b-input>
      </b-field>
      <b-field label="Nim">
        <b-input required v-model="nim" min="15" type="number"></b-input>
      </b-field>
      <b-field label="Email">
        <b-input v-model="user.email" readonly></b-input>
      </b-field>
      <b-field label="Prodi">
        <b-select v-model="prodi" placeholder="Pilih Prodi" required>
          <option value="Tif">Teknik Informatika</option>
          <option value="Si">Sistem Informasi</option>
        </b-select>
      </b-field>
      <b-field label="ID Line">
        <b-input v-model="line" required></b-input>
      </b-field>
      <b-field label="Pilihan 1">
        <b-select
          v-model="daftar.pilihan1"
          required
          placeholder="Pilih Biro/Kementrian"
          v-on:change="checked()"
        >
          <optgroup label="Kementrian">
            <option
              v-for="item in mentri"
              v-if="daftar.pilihan2 != item.info"
              v-bind:value="item.info"
            >{{item.info}}</option>
          </optgroup>

          <optgroup label="Kebiroan">
            <option
              v-for="item in biro"
              v-if="daftar.pilihan2 != item.info"
              v-bind:value="item.info"
            >{{item.info}}</option>
          </optgroup>
        </b-select>
      </b-field>
      <b-field label="Alasan">
        <b-input v-model="alasan1" required maxlength="100" type="textarea"></b-input>
      </b-field>
      <b-field label="Pilihan 2">
        <b-select v-model="daftar.pilihan2" required placeholder="Pilih Biro/Kementrian">
          <optgroup label="Kementrian">
            <option
              v-for="item in mentri"
              v-if="daftar.pilihan1 != item.info"
              v-bind:value="item.info"
            >{{item.info}}</option>
          </optgroup>

          <optgroup label="Kebiroan">
            <option
              v-for="item in biro"
              v-if="daftar.pilihan1 != item.info"
              v-bind:value="item.info"
            >{{item.info}}</option>
          </optgroup>
          <option value="Tidak Memilih ">Tidak Memilih</option>
        </b-select>
      </b-field>
      <b-field label="Alasan">
        <b-input v-model="alasan2" required maxlength="100" type="textarea"></b-input>
      </b-field>
      <b-message
        title="Danger"
        type="is-danger"
        v-if="isNimRight"
      >Your nim are detected not started with 17 or 18</b-message>
      <button type="submit" class="button is-primary is-active" v-if="!loading">Daftar</button>
      <button type="submit" class="button is-primary is-loading" v-if="loading">Daftar</button>
    </form>
  </section>
</template>
<script>
import router from "@/router";
import Joi from "joi";
import { mapActions, mapState, mapGetters } from "vuex";
const URL = "http://bem.filkom.ub.ac.id/api/postdata.php";
const URLCHECK = "http://bem.filkom.ub.ac.id/api/getown.php";
// const URL = "http://localhost/backend/postdata.php";
// const URLCHECK = "http://localhost/backend/658802_getown.php";
export default {
  name:"Registrasion",
  computed: {
    ...mapGetters({
      dataUser: "auth/getUser"
    }),
    ...mapState("auth", ["user", "isLoggedIn"])
  },
  data: () => ({
    daftar: {
      nim: "",
      prodi: "",
      line: "",
      pilihan1: "",
      alasan1: "",
      pilihan2: "",
      alasan2: ""
    },
    selected1: null,
    isRegistered: false,
    test: "hello World",
    loading: false,
    isNimRight: false,
    mentri: [
      { info: "Direktorat Jendral Dalam Negeri" },
      { info: "Direktorat Jendral Luar Negeri" },
      { info: "Direktorat Jendral Kaderisasi" },
      { info: "Direktorat Jendral Pengembangan dan Pemberdayaan Manusia" },
      { info: "Kementrian Inovasi Karya" },
      { info: "Direktorat Jendral Advokasi dan Kebijakan Kampus" },
      { info: "Direktorat Jendral Kesejahteraan Mahasiswa" },
      { info: "Kemenrian Kajian dan Aksi Strategis" },
      { info: "Kementrian Sosial Masyarakat" }
    ],
    biro: [
      { info: "Biro Bisnis Dan Kemitraan" },
      { info: "Pengembangan Informasi Dan Teknologi" },
      { info: "Biro Administrasi" },
      { info: "Biro Keuangan" },
      { info: "Biro Hubungan Masyarakat" },
      { info: "Biro Media Kreatif" }
    ]
  }),
  methods: {
    ...mapActions("auth", ["login", "logout"]),
    checked() {
      console.log("hello");
    },
    register() {
      const nim = this.nim;
      const checkNim = nim.startsWith("17") || nim.startsWith("18");
      if (checkNim) {
        const send = {
          nama: this.dataUser.name,
          email: this.dataUser.email,
          nim: this.nim,
          prodi: this.prodi,
          idline: this.line,
          pilihan1: this.daftar.pilihan1,
          alasan1: this.alasan1,
          pilihan2: this.daftar.pilihan2,
          alasan2: this.alasan2
        };
        this.loading = true;
        console.log(this.loading);
        fetch(URL, {
          method: "POST",
          body: JSON.stringify(send),
          headers: {
            "content-type": "application/json",
            "access-control-allow-origin": "*"
          }
        })
          .then(ress => {
            console.log(ress);
            this.loading = false;
            console.log(this.loading);
            router.push("/sukses");
          })
          .catch(err => {
            router.push("/registrasion");
            console.log(err);
            this.loading = false;
          });
      } else {
        this.isNimRight = true;
      }
    },

    async areRegistered() {
      let status;
      const email = {
        email: this.dataUser.email
      };
      await fetch(URLCHECK, {
        method: "POST",
        body: JSON.stringify(email)
      })
        .then(res => {
          if (res.ok) return res.json();
        })
        .then(result => {
          console.log(result.status);
          this.isRegistered = result.status;
          if (this.isRegistered) {
            router.push("/sukses");
          }
        });
    }
  },
  beforeMount() {
    this.areRegistered();
  }
};
</script>

